import attendanceRecordimg from '@/assets/mine/icon1.png'
import checkRecordimg from '@/assets/mine/icon2.png'
import leaveRecordimg from '@/assets/mine/icon3.png'
import settingimg from '@/assets/mine/icon4.png'
import { MessageBox } from 'mint-ui'

export default {
  data () {
    return {
      mineArr: [
        {
          id: 'clockInRecord',
          name: '考勤记录',
          imgsrc: attendanceRecordimg
        },
        {
          id: 'checkTab',
          name: '审核记录',
          imgsrc: checkRecordimg
        },
        {
          id: 'vacationList',
          name: '请假记录',
          imgsrc: leaveRecordimg
        },
        {
          id: 'setting',
          name: '退出账号',
          imgsrc: settingimg
        }
      ],
      orgname: '', // 部门
      name: '', // 姓名
      jobType: ''
    }
  },
  created () {
    this.name = this.$store.getters.getUser.data.name
    this.orgname = this.$store.getters.getUser.data.orgname
    this.jobType = this.$store.getters.getUser.data.jobType
  },
  methods: {
    // 跳转到指定页面
    toRouter (id) {
      if (id === 'setting') {
        this.$store.dispatch('loginout', this)
      } else if (id === 'checkTab') {
        if (this.jobType === '01' || this.jobType === '02') {
          MessageBox.alert('普通员工暂无审批权限', '操作提示')
        } else {
          this.$router.push({ name: id })
        }
      } else {
        this.$router.push({ name: id })
      }
    }
  }
}



